.mobileModeSettingConatiner {
    max-height: calc(100vh - 120px);
    height: calc(100vh - 120px);
    overflow: auto;
    background-color: #F2F9FF;
    padding: 22px 16px 0;
    box-sizing: border-box;
}

.mobileModeSettingFooter {
    height: 64px;
    background-color: #ECEFF1;
    padding: 0 24px;
    color: #061D30;
    .activeFooterMenu {
        height: 40px;
        background-color: #3787C9;
        border-radius: 20px;
        color: #FFFFFF;
        padding: 0 16px;
    }
}